
* {
  margin: 0;
  color: rgb(107, 107, 107);
}

.navbar{
  padding:20px;
  display:flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #db6363;
}

.navbar h1 {
  color: #e24776;
}

.navbar .links{
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  padding: 6px;
  text-decoration: none;
}

.navbar a:hover{
  color: #fa004b;
}


.content {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
}


.book-headline {
  text-align: center;
  padding: 20px 0;
}
.book{
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #979090;
}


.book h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.book a{
  text-decoration: none;
}

.book h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}

.book-details div {
  margin: 20px 0;
}

.book-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}


.column-leftbar {
  float: left;
  width: 25%;
  padding: 10px;
  height: 300px; /* Should be removed. Only for demonstration */
}

.column {
  float: left;
  width: 60%;
  padding: 10px;
  height: 300px; /* Should be removed. Only for demonstration */
}

.addbook
{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;

}

.addbook label {
  text-align: left;
  display:  block;
}


.addbook h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}

.addbook-upload{
  display: inline-block;
  padding: 20px;
}

.addbook-form input, .addbook-form textarea, .addbook-form select{
  width: 100%;
  padding: 6px 10px;
  margin:10px 0;
  box-sizing: border-box;
  display: block;
}

.editbook
{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;

}

.editbook label {
  text-align: left;
  display:  block;
}


.editbook h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}

.editbook input, .editbook textarea, .editbook select{
  width: 100%;
  padding: 6px 10px;
  margin:10px 0;
  box-sizing: border-box;
  display: block;
}


.book-details
{
  max-width: 400px;
  margin: 0 auto;
  
}


.book-details h2{
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}

.book-details div {
  margin: 20px 0;
}

.book-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  margin: 0 5px;
  text-align: center;
}

.column-leftbar li{

  font-size: 20px;
  color:#474747;
  margin: 5px 0;
  text-transform: capitalize;
}
.column-leftbar li:hover{
  color: rgb(0, 0, 0);
  cursor: pointer;
  font-weight: bold;
}

.column-leftbar ul{
  padding: 20px 0;
  margin: 0;
} 

.book-details-text{
  padding: 20px;
  margin: 0;
}
.book-details-text p {
  margin: 5px 0;
}

.img {
  padding: 0 20px;
  margin: 'auto';
  display: 'block';
  margin: 0;
  height: 128px;
}